.container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Estilizando imagem no mostrador*/ 
.imgContainer{
    width: 21.4375rem;
    height: 15rem;
    background-repeat: none;
    background-size: cover;
    border-radius: 10px 10px 0 0;
    margin-top: 1.75rem;
    background-image: url(image-product-mobile.jpg);
}

/*Estilizando a parte de baixo*/
.bodyConteiner{
    background-color: white;
    width: 21.4375rem;
    display: flex;
    justify-content: center;
    border-radius: 0 0 10px 10px;
}

.textContainer{
    width: 18rem;
}

.textContainer__title{
    margin-top: 1.75rem;
    letter-spacing: 5px;
    text-transform: uppercase;
    font-size: 12px;
    color: hsl(228, 12%, 48%);
}

.textContainer__subtitle{
    margin-top: 1rem;
    font-family: 'Fraunces', serif;
    font-weight: 700;
    font-size: 2rem;
    color: hsl(212, 21%, 14%);
}

.textContainer__paragraph{
    margin-top: 1rem;
    font-size: 14px;
    line-height: 24px;
    color: hsl(228, 12%, 48%);
}

.textContainer__price{
    margin-top: 1rem;
    display: flex;
    align-items: center;
}

.textContainer__price--actual{
    font-family: 'Fraunces', serif;
    font-size: 2rem;
    font-weight: 700;
    color: hsl(158, 36%, 37%);
}

.textContainer__price--before{
    margin-left: 1rem;
    font-size: 14px;
    text-decoration:line-through;
    color: hsl(228, 12%, 48%);
}

.textContainer__chart{
    margin-top: 1rem;
    margin-bottom: 1.5rem;
    height: 3rem;
    border-radius: 5px;
    width: 18rem;
    border: none;
    background-color: hsl(158, 36%, 37%);
    color: white;
    font-weight: 700;
}

@media screen and (min-width: 376px){
    /* Estilizando o body*/
    body{
        width: 100vw;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .container{
        flex-direction: row;
        max-width: 600px;
        max-height: 450px;
    }

    /* Estilizando imagem no mostrador*/ 
    .imgContainer{
        width: 300px;
        height: 450px;
        background-image: url(image-product-desktop.jpg);
        border-radius: 10px 0 0 10px;
        margin-top: 0;
    }

    .bodyConteiner{
        width: 300px;
        height: 450px;
        border-radius: 0 10px 10px 0;
    }

    .textContainer{
        margin: 0 auto;
        width: 15rem;
    }

    .textContainer__subtitle{
        margin-top: 1.25rem;
    }
    
    .textContainer__paragraph{
        margin-top: 1.25rem;
    }
    
    .textContainer__price{
        margin-top: 1.25rem;
    }
    
    .textContainer__price--actual{
        margin-top: 1.25rem;
    }
    
    .textContainer__price--before{
        margin-top: 1.25rem;
    }
    
    .textContainer__chart{
        margin-top: 1.25rem;
        width: 15rem;
        cursor: pointer;
    }

    .textContainer__chart:hover{
        background-color: rgb(26,64,49);
    }


}